<template>
  <div>
    <h1>Processing Login...</h1>
  </div>
</template>

<script>
import { onMounted } from "vue";

export default {
  name: "KakaoCallback",
  setup() {
    onMounted(async () => {
      const urlParams = new URLSearchParams(window.location.search);
      const code = urlParams.get("code");

      if (code) {
        console.log("Authorization Code:", code);

        try {
          // 액세스 토큰 요청
          const response = await fetch(`https://kauth.kakao.com/oauth/token`, {
            method: "POST",
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
            },
            body: new URLSearchParams({
              grant_type: "authorization_code",
              client_id: "YOUR_KAKAO_CLIENT_ID", // 카카오 REST API 키
              redirect_uri: "http://localhost:3000/callback", // 동일한 리다이렉트 URI
              code: code,
            }),
          });

          const tokenData = await response.json();
          if (tokenData.access_token) {
            console.log("Access Token:", tokenData.access_token);

            // 액세스 토큰을 로컬 스토리지에 저장
            localStorage.setItem("kakaoAccessToken", tokenData.access_token);

            // 사용자 정보 요청
            fetchUserProfile(tokenData.access_token);
          } else {
            console.error("Failed to get access token:", tokenData);
          }
        } catch (error) {
          console.error("Error during token request:", error);
        }
      } else {
        console.error("No authorization code found.");
      }
    });

    // 사용자 정보 요청 함수
    const fetchUserProfile = async (accessToken) => {
      try {
        const response = await fetch("https://kapi.kakao.com/v2/user/me", {
          method: "GET",
          headers: {
            Authorization: `Bearer ${accessToken}`,
          },
        });

        const userProfile = await response.json();
        console.log("User Profile:", userProfile);
        alert(`Welcome, ${userProfile.properties.nickname}`);
      } catch (error) {
        console.error("Failed to fetch user profile:", error);
      }
    };
  },
};
</script>
